<template>
    <div class="page">
        业务包添加页面

    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    export default {
        data() {
            return {
                editable: false,

            }
        },
        computed: mapGetters(['loginMsg']),
        mounted() {

        },
        methods: {
            //提交表单
            submitForm() {
                var vm = this;
                vm.editable = false;
                var par = {
                    "status": vm.loginMsg.status,
                    "name": vm.loginMsg.name,
                    "token": vm.loginMsg.token,
                    "avatar":vm.loginMsg.avatar,
                    "sex": vm.loginMsg.sex,
                    "introduction": vm.loginMsg.introduction,
                };
                this.$store.commit("editUserINfo",par);
                
            },
            //取消
            resetForm(){
               this.editable=false;
            },
            //上传头像
            uploadAvatar(){
                alert('稍后补上')
            },
        }
    };
</script>

<style lang="scss">
    .app-container{
        margin:10px 1% 0;
        width:97%;
        overflow-x: hidden;
    }
    .user_img {
        /*width: 80%;*/
        padding: 20px;
        text-align: center;
        border: 1px solid #dee1e2;
        img {
            max-width: 200px;
            max-height: 200px;
            border-radius: 50%;
        }
    }
    .user_info_form label,.user_info_text label{
        font-weight: bold;
    }
    .user_info {
        padding-bottom: 20px;
        border: 1px solid #dee1e2;
        font-size:14px;
        h2 {
            margin: 0;
            font-weight: bold;
            padding: 10px 20px;
            border-bottom: 1px solid #dee1e2;
            .i_edit {
                float: right;
                font-size: 14px;
                color: #7ab8ed;
            }
        }
    }

    .user_info_form, .user_info_text {
        padding: 20px;
    }
</style>



